body {
  margin: 0;
  padding: 0;
  font-family: "Courier New";
background-image: -webkit-linear-gradient(left, #d612da, #a759f3, #7277fd, #3c8af9, #1297eb);
background-image: linear-gradient(to right, #d612da, #a759f3, #7277fd, #3c8af9, #1297eb);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.parti
{
  position:fixed;
  top:0;
  bottom:0;
  left:0;
  right:0;
  z-index: -1;
}

.center {
  display: flex;
  justify-content: center;
}

.Tilt
{
  background-image: -webkit-linear-gradient(left, #d612da, #a759f3, #7277fd, #3c8af9, #1297eb);
  background-image: linear-gradient(to right, #d612da, #a759f3, #7277fd, #3c8af9, #1297eb);

}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
  z-index:1;
}

